header {
	h1 {
		font-family: "Quicksand", sans-serif;
		font-size: 45px;
		font-weight: 100;
		text-align: center;
		text-transform: uppercase;
		letter-spacing: -2px;
		margin: 20px 0 40px;
	}
}

main {
	position: relative;
	flex: 1;
	display: flex;
	align-items: stretch;
	justify-content: space-between;
	margin: 0 0 40px;

	section {
		display: flex;
		flex-direction: column;
		flex: 1;
		padding: 50px 20px;
		transition: 0.5s ease-in-out;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			width: 50%;
			height: 100%;
			flex: 1;
			opacity: 0;
			z-index: 1;
			transition: 0.5s ease-in-out;
		}

		h2 {
			position: relative;
			font-size: 20px;
			font-weight: 300;
			color: #9f9f9f;
			z-index: 2;
		}

		&:last-child {
			h2 {
				text-align: right;
			}
		}

		form {
			background-color: #fff;
			position: absolute;
			top: 90px;
			left: 20px;
			width: calc(100vw - 40px);
			padding: 50px 20px 120px;
			margin: 0 0 30px;
			border-radius: 10px;
			box-shadow: 0 0 25px 2px rgba(#666, 0.3);
			z-index: 2;
			transform: scaleY(0);
			transform-origin: 0 0;
			transition: 0.5s ease-in-out;

			.button-container {
				position: absolute;
				top: 100%;
				left: 0;
				width: 100%;
				transform: translateY(-50%);
				z-index: 3;

				button {
					box-shadow: 0 0 25px 2px rgba(#666, 0.3);
				}
			}

			.errorMessage {
				font-size: 14px;
				color: #c00;
				margin: 10px 0 0;
			}

			.wave-bottom {
				position: absolute;
				bottom: 0;
				left: 0;
				display: block;
				width: 100%;
				z-index: 2;
				border-bottom-left-radius: 10px;
				border-bottom-right-radius: 10px;
			}
		}

		&.sec-login {
			form {
				a {
					font-size: 14px;
					color: #bfbfbf;
					text-align: right;
					text-decoration: none;
					margin: 30px 0 0;

					&:hover {
						text-decoration: underline;
					}
				}
			}

			&::before {
				left: 0;
			}
		}

		&.sec-sign-up {
			h2 {
				text-align: right;
			}

			&::before {
				right: 0;
			}
		}

		&.current {
			&::before {
				opacity: 1;
				box-shadow: 0 0 25px 4px rgba(#666, 0.3);
			}

			&.sec-login {
				&::before {
					background: linear-gradient(
						to bottom,
						#4496ff,
						#987fd3,
						#d069bd
					);
					border-bottom-right-radius: 30px;
					border-top-right-radius: 30px;
				}

				& ~ .arrow-select-current {
					left: 80px;
				}
			}

			&.sec-sign-up {
				&::before {
					background: linear-gradient(
						to bottom,
						#768aeb,
						#aa75c5,
						#f160ad
					);
					border-bottom-left-radius: 30px;
					border-top-left-radius: 30px;
				}

				& ~ .arrow-select-current {
					left: 185px;
				}
			}

			h2 {
				color: #fff;
			}

			form {
				display: block;
				transform: scaleY(1);
				transition-delay: 0.5s;
			}
		}
	}

	.arrow-select-current {
		position: absolute;
		top: 0px;
		width: 100px;
		height: 91px;
		overflow: hidden;
		z-index: 2;
		transition: 0.3s 0.3s ease-in-out;

		&::after {
			content: "";
			position: absolute;
			width: 60px;
			height: 60px;
			background: #fff;
			transform: rotate(45deg); /* Prefixes... */
			top: 65px;
			left: 25px;
			border-top-left-radius: 5px;
			box-shadow: 0 0 25px 2px rgba(#666, 0.3);
		}
	}
}
